<template>
  <div class="login-page">
    <!-- Background Elements -->
    <div class="login-background">
      <div class="bg-gradient"></div>
      <div class="bg-pattern"></div>
    </div>

    <div class="login-container">
      <!-- Login Card -->
      <div class="login-card">
        <!-- Header -->
        <div class="login-header">
          <div class="logo-container">
            <Icon icon="mdi:shield-account" class="logo-icon" />
          </div>
          <h1 class="login-title">Masuk ke WBS</h1>
          <p class="login-subtitle">
            Masuk dengan email atau username untuk mengakses sistem pelaporan<br>
            <small style="color: #9ca3af; font-size: 0.875rem;">
              Contoh: user@email.com atau username123
            </small>
          </p>
        </div>

        <!-- Login Form -->
        <el-form 
          ref="loginForm" 
          :model="loginForm" 
          :rules="loginRules" 
          class="login-form"
          @submit.native.prevent="handleLogin"
        >
          <el-form-item prop="email" class="form-item">
            <el-input
              v-model="loginForm.email"
              placeholder="Email atau Username"
              prefix-icon="el-icon-user"
              size="large"
              class="form-input"
              @input="formatEmailOrUsername"
            />
          </el-form-item>

          <el-form-item prop="password" class="form-item">
            <el-input
