<script>
import { Icon } from "@iconify/vue2";
import { mapActions, mapState, mapGetters } from 'vuex';

export default {
  name: 'LoginView',
  components: {
    Icon
  },
  computed: {
    ...mapState(['loading', 'message', 'status']),
    ...mapGetters(['isLoggedIn'])
  },
  watch: {
    // Watch for store message changes and display them
    message(newMessage) {
      if (newMessage) {
        this.$message.error(newMessage);
        // Clear message after showing it
        this.$store.commit('set_message', null);
      }
    },
    
    // Watch for login status changes
    isLoggedIn(newValue) {
      if (newValue) {
        // User just logged in, navigate to dashboard
        // Use $nextTick to ensure the state is fully updated
        this.$nextTick(() => {
          this.$router.push({ name: 'dashboard-home' }).catch(error => {
            // Ignore navigation errors caused by guards
            console.log('Navigation handled by guard:', error.message);
          });
        });
      }
    }
  },
  data() {
    return {
      loginForm: {
        email: '',
        password: '',
        remember: false
      },
      loginRules: {
        email: [
          { required: true, message: 'Email atau Username wajib diisi', trigger: 'blur' },
          { 
            validator: (rule, value, callback) => {
              if (!value) {
                callback();
                return;
              }
              
              // Check if it's a valid email format
              const emailPattern = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
              // Check if it's a valid username format (alphanumeric, underscore, dash, 3-20 chars)
              const usernamePattern = /^[a-zA-Z0-9_-]{3,20}$/;
              
              if (emailPattern.test(value) || usernamePattern.test(value)) {
                callback();
              } else {
                callback(new Error('Masukkan email yang valid atau username (3-20 karakter, huruf, angka, _, -)'));
              }
            }, 
            trigger: ['blur', 'change'] 
          }
        ],
        password: [
          { required: true, message: 'Password wajib diisi', trigger: 'blur' },
          { min: 6, message: 'Password minimal 6 karakter', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    ...mapActions(['login']),
    
    formatEmailOrUsername(value) {
      // Trim whitespace and convert username to lowercase (keep email case for domain)
      let formattedValue = value.trim();
      
      // If it looks like a username (no @ symbol), convert to lowercase
      if (!formattedValue.includes('@')) {
        formattedValue = formattedValue.toLowerCase();
        // Remove any invalid characters for username
        formattedValue = formattedValue.replace(/[^a-zA-Z0-9_-]/g, '');
      }
      
      this.loginForm.email = formattedValue;
    },

    async handleLogin() {
      this.$refs.loginForm.validate(async (valid) => {
        if (valid) {
          try {
            // Prepare credentials for store action
            const credentials = {
              usernameOrEmail: this.loginForm.email,
              password: this.loginForm.password
            };

            // Call store action for login
            const response = await this.login(credentials);

            // Handle successful login
            if (response.data) {
              // Store additional user data if provided
              if (response.data.user) {
                localStorage.setItem('user_data', JSON.stringify(response.data.user));
              }

              this.$message.success('Login berhasil!');
              
              // Let navigation guard handle the redirect automatically
              // No manual navigation needed here
            }
          } catch (error) {
            console.error('Login error:', error);
            // Error handling is managed by store and watcher
          }
        } else {
          this.$message.error('Silakan periksa form input Anda');
        }
      });
    },

    // Test login dengan kredensial yang diberikan
    async testLogin() {
      try {
        // Prepare test credentials for store action
        const credentials = {
          usernameOrEmail: '6mdiames',
          password: 'd&6yecU3MQ3J'
        };

        // Call store action for test login
        const response = await this.login(credentials);

        if (response.data) {
          // Store additional user data if provided
          if (response.data.user) {
            localStorage.setItem('user_data', JSON.stringify(response.data.user));
          }

          this.$message.success('Test login berhasil!');
          // Let navigation guard handle the redirect automatically
        }
      } catch (error) {
        console.error('Test login error:', error);
        // Error will be handled by store watcher
      }
    },
    
    goToRegister() {
      this.$router.push('/register');
    }
  }
}
</script>

